	module.exports = {"data":{"markdownRemark":{"html":"<p>Here's a neat article on scholarly blogs from 2003 in the Chronicle of Higher Education. It's outdated but excellent, framing the issue in real-world concerns of \"academic posturing\" vs. \"realtime idea exchanges.\"</p>\n<p>Here's what I mean:</p>\n<blockquote class=\"large\"> \"In their skeptical moments, academic bloggers worry that the medium smells faddish, ephemeral. But they also make a strong case for blogging's virtues, the foremost of which is freedom of tone. Blog entries can range from three-word bursts of sarcasm to carefully honed 5,000-word treatises. The sweet spot lies somewhere in between, where scholars tackle serious questions in a loose-limbed, vernacular mode.\"</blockquote><a href=\"http://chronicle.com/free/v49/i39/39a01401.htm\"> Read the article.</a>","frontmatter":{"created_at":"April 23, 2005","path":"/2005/04/23/ivory-tower-blogs","title":"Ivory Tower Blogs","tags":["education","blogging","gradschool"]}}},"pathContext":{}}

